.tableWidth{
    width: 100%;
}
.tdWidth{
    width: 210px;
}

.trMiddle{
    vertical-align: middle;
}

.commandItem{
    margin-bottom: 15px;
}

.commandText{
    border: 0;
    color: #ffffff;
    width: 100%;
    caret-color: transparent;
}

.commandText:focus, .commandText:focus{
    outline: none;
}

.terminalBody {
    position: fixed;
    padding: 10px;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: auto;
}

.terminalBottom{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
}

.blinkWrapper{
    position: relative;
}

.text { 
  position: relative;
  display: inline-block;
  font-family: Monaco, Arial, Helvetica, sans-serif;
  line-height: 24px;
  font-size: 24px;
  color: white;
}

.text::after {
  content: "";
  /* Remove display: inline-block if not required to be on the same line as text etc */
  display: inline-block;
  background-color: #606060;
  vertical-align: top;
  width: 8px;
  height: 16px;
  -webkit-animation: blink 1s step-end infinite;
  animation: blink 1s step-end infinite;
}

@-webkit-keyframes blink {
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
  100% { opacity: 1.0; }
}

@keyframes blink {
  0% { opacity: 1.0; }
  50% { opacity: 0.0; }
  100% { opacity: 1.0; }
}